@import '../core/variables.scss';
@import '../core/mixins.scss';

// About me---
.aboutMe {
  background: url('../../images/background-old9.jpg') no-repeat;
  background-position: 0%;
  background-size: cover;
  width: 100%;
  min-width: 360px;
  height: auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.about_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}

.about_mainTitle {
  color: $colorMain-h2;
  text-align: center;
  padding-bottom: 12px;
  letter-spacing: 4px;
  word-spacing: 8px;
  font-size: 2.2rem;
  font-weight: 900;
  @include tablet {
    font-size: 2.5rem;
    letter-spacing: 6px;
    word-spacing: 10px;
  }
}

.about_text {
  padding: 0 2rem 1rem 2rem;
  font-size: 0.8rem;
  line-height: 1.2rem;
  text-align: center;
  @include tablet {
    line-height: 3rem;
    padding: 0 3rem 2.7rem 3rem;
    font-size: 1rem;
  }
}

.link_download {
  border-radius: 10px;
  border: solid 2px $colorCover-h1;
  background-color: $colorMain-h2;
  color: $color-rrss;
  padding: 0.2rem;
  margin: 0.2rem;
  text-decoration: none;
  @include tablet {
    font-weight: bolder;
    padding: 0.5rem;
    margin: 1rem;
  }
}
//Tech---
.tech_container {
  padding: 7rem 0 1rem 0;
}
.tech_title {
  text-align: center;
  padding-bottom: 15px;
  letter-spacing: 4px;
  word-spacing: 8px;
  color: $colorMain-h2;
  font-size: 30px;
  font-weight: 800;
  @include tablet {
    font-size: 35px;
    padding-bottom: 25px;
  }
}

.tech_list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 10px;
  grid-row-gap: 15px;
  padding-left: 20px;
  padding-bottom: 25px;
  @include tablet {
    grid-template-columns: repeat(6, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    padding-left: 100px;
    padding-right: 30px;
  }
}

.tech_item {
  background-color: $colorAbout-tech;
  padding: 15px;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  justify-items: center;
  -webkit-align-items: center;
  align-items: center;
  border-radius: 50%;
  width: 70px;
  height: 70px;
}

.tech_icon {
  width: 35px;
  height: 35px;
  @include tablet {
    width: 30px;
    height: 30px;
  }
}

// Projects---
.projects {
  background: linear-gradient(
    #f8f8fa,
    $color-rrss,
    $colorProjects-desc,
    $color-rrss,
    #f8f8fa
  );
  width: 100%;
  min-width: 360px;
  height: auto;
  min-height: 100vh;
}

.projects_mainTitle {
  color: $colorMain-h2;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 25px;
  letter-spacing: 4px;
  word-spacing: 8px;
  font-size: 30px;
  font-weight: 900;
  @include tablet {
    font-size: 35px;
    letter-spacing: 6px;
    word-spacing: 10px;
  }
}

.projects_listContainer {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 15px;
  padding-bottom: 35px;
  @include tablet {
    grid-template-columns: repeat(2, 1fr);
  }
}

.projects_listItem {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-column-gap: 15px;
  grid-row-gap: 15px;
}

.item_title {
  text-align: center;
  padding-top: 15px;
  padding-bottom: 5px;
  grid-column: span 2;
  color: $colorProjects-titleItem;
  font-size: 20px;
  @include tablet {
    font-size: 28px;
  }
}

.img_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.item_img {
  width: 300px;
  border-radius: 12%;
  box-shadow: 0.3em 0.3em 0.5em $colorMain-h2;
}

.item_textContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.item_description {
  font-size: 14px;
  text-align: center;
  width: 80%;
  background-color: $colorProjects-desc;
  box-shadow: 0.1em 0.1em 0.2em #525151;
  border-radius: 10%;
  padding: 10px;
}

.projectIcon {
  color: $colorProjects-icon;
  width: 20px;
  font-size: 35px;
}
